import React, {Component} from 'react';
import LatestProductsHome from '../../components/Home/LatestProductsHome';
import CategorySidebar from '../../components/LeftSidebar/CategorySidebar';
import ItemProduct from "../../components/Product/ItemProduct";
import Breadcrumb from '../../components/Breadcrumb';
import * as Actions from "../../actions";
import {connect} from "react-redux";
import Loading from "../../components/Loading";
import Pagination from "react-pagination-library";
import "react-pagination-library/build/css/index.css";
import {Helmet} from 'react-helmet';
const loadJs = require('loadjs');

class Index extends Component {
    constructor(props) {
        super(props);
        this.state = {
            currentPage: 1,
            params: {
                page: 1,
                idCategory: 0
            }
        };
        this.myRef = React.createRef();
    }

    changeCurrentPage = numPage => {
        this.setState({
            currentPage: numPage,
            params: {
                    ...this.props.params,
                    page: numPage
                }
        }, () => this.props.fetchData(this.state.params)
        );
        this.myRef.current.scrollIntoView({ behavior: 'smooth'});
        //window.scrollTo(0, this.myRef.current.offsetTop); //cach 2
        //fetch a data
        //or update a query to get data
    };

    componentDidMount() {
        loadJs('/js/main.js');

        let id = this.props.match.params.id;
        this.setState({
                params: {
                    page: 1,
                    idCategory: id
                }
            }, () => this.props.fetchData(this.state.params)
        );
    }

    showData = () => {
        if (this.props.data != null && this.props.fetching === false) {
            return this.props.data.data.map((item,key) => {
                    return (
                        <div key={key} className="col-lg-4 col-md-6 col-sm-6">
                            <ItemProduct data={item}/>
                        </div>
                    );
                })
        } else {
            return <Loading />
        }
    }

    render() {
        const moduleName = 'Sản phẩm';
        return (
            <div>
                <Helmet>
                    <title>{this.props.data ? this.props.data.title : moduleName}</title>
                </Helmet>
                <Breadcrumb module={moduleName} title={this.props.data ? this.props.data.title : moduleName}/>
                <section className="product spad">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-3 col-md-5">
                                <div className="sidebar">
                                    <div className="sidebar__item">
                                        <CategorySidebar data={this.props.dataDefault}/>
                                    </div>
                                    <div className="sidebar__item">
                                        <LatestProductsHome data={this.props.dataDefault}/>
                                    </div>
                                </div>
                            </div>
                            <div className="col-lg-9 col-md-7">
                                <div className="product__discount">
                                    <div className="section-title product__discount__title">
                                        <h2>Sản phẩm</h2>
                                    </div>
                                </div>
                                <div ref={this.myRef} className="row">
                                    {this.showData()}
                                </div>
                                <Pagination
                                    currentPage={this.state.currentPage}
                                    totalPages={this.props.data ? this.props.data.totalPages : 0}
                                    changeCurrentPage={this.changeCurrentPage}
                                    theme="bottom-border border-bottom"
                                />
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        fetching: state.payloadProduct.fetching,
        data: state.payloadProduct.data,
        error: state.payloadProduct.error,
        dataDefault: state.payloadDataDefault.data
    };
};

const mapDispatchToProps = dispatch => {
    return {
        fetchData: (params) => dispatch(Actions.productAction.requestProductData(params))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Index);