import React, {Component} from 'react';
import ProductCategory from '../ProductCategory/itemProductCategory';

class CategorySidebar extends Component {
    showCategory = () => {
        return this.props.data.categoryProduct.map((item,key) => {
            return <ProductCategory data={item} key={key}/>
        })
    }

    render() {
        return (
            <div>
                <h4>Danh mục</h4>
                <ul>
                    {this.showCategory()}
                </ul>
            </div>
        );
    }
}

export default CategorySidebar;